import React from 'react'
import { Card, Table } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import { useCollection } from "react-firebase-hooks/firestore";
import { DataService } from "services/fuel.service";
import NumberFormat from 'react-number-format';


const { Title } = Typography;

const FuelHistoric = ({systemData}) => {

  const [fuels, fuelsLoading] = useCollection(DataService.getAll());
  const { allTrucks, allCountries, allCountriesLoading, allCurrencyLoading, allCurrency, allRentedTrucks} = systemData;

	const tableColumns = [
		{
			title: 'Date',
			dataIndex: 'id',
			render: (_,record) => new Date(+record.data()._id).toLocaleString()
		},
		{
			title: 'Truck',
			dataIndex: 'id',
			render: (_,record) => record.data().internal ? 
			`${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`
			:
			`${allRentedTrucks[record.data().truckId].plateNumber} - ${allRentedTrucks[record.data().truckId].driverName}`
		},
		{
			title: 'Meter Number',
			dataIndex: 'meterNumber',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().meterNumber} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Liters',
			dataIndex: 'liters',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().liters} 
							  suffix={'L'} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Price/L',
			dataIndex: 'literPrice',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().literPrice} 
							  suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		
		{
			title: 'Rate',
			dataIndex: 'exchangeRate',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().exchangeRate} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Total RWF',
			dataIndex: 'totalAmount',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().totalAmount} 
							  suffix={' RWF'} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Use',
			dataIndex: 'use',
			render: (_,record) => record.data().orderId ? 'For Trip' : 'Internal Use'
		},
		{
			title: 'Station',
			dataIndex: 'station',
			render: (_,record) => record.data().station
		},
		{
			title: 'Country',
			dataIndex: 'country',
			render: (_,record) => allCountriesLoading ? '' :  allCountries[record.data().country]?.name 
		},
	];

	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Fuel historic</Title>
					</div>
				</Flex>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={!fuelsLoading ? fuels.docs.reverse() : []} 
					loading={fuelsLoading}
					rowKey='_id' 
				/>
			</div>
		</Card>
	)
}

export default FuelHistoric
